/* Basic styling */
body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
}

.app-container {
  text-align: center;
  padding: 20px;
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Controls Container */
.controls-container {
  margin-bottom: 20px;
}

/* Buttons */
.control-button,
.download-button {
  padding: 12px 24px;
  font-size: 16px;
  cursor: pointer;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  margin: 10px;
  transition: background-color 0.3s;
}

.control-button:hover,
.download-button:hover {
  background-color: #45a049;
}

/* Canvas Container */
.canvas-container {
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin: 0 auto 20px;
}

.video-container {
  margin-top: 20px;
}

.video-player {
  width: 100%;
  max-width: 400px;
  height: auto;
}

.download-button {
  margin-top: 10px;
}

.rive-container {
  width: 100%; /* Set to 100% or a specific size */
  height: 100%; /* Set to 100% or a specific size */
  display: flex;
  justify-content: center;
  align-items: center;
}

.rive-animation {
  width: 100%;
  height: 100%;
}

.toggle-label {
  display: inline-flex;
  align-items: center;
  margin: 0 10px;
  cursor: pointer;
}

.toggle {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
  margin-left: 10px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle span {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 24px;
}

.toggle span:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

.toggle input:checked + span {
  background-color: #4caf50;
}

.toggle input:checked + span:before {
  transform: translateX(26px);
}

.debug-container {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 160px;
  height: 120px;
  border: 1px solid #ccc;
  overflow: hidden;
  z-index: 1000;
  background-color: #000;
}

.debug-container video,
.debug-container canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rotation-controls {
  margin: 20px 0;
  padding: 15px;
  background-color: #f5f5f5;
  border-radius: 5px;
  width: 100%;
  max-width: 300px;
  margin: 20px auto;
}

.rotation-slider {
  display: flex;
  align-items: center;
  margin: 10px 0;
  gap: 10px;
}

.rotation-slider label {
  min-width: 80px;
  text-align: right;
}

.rotation-slider input[type="range"] {
  flex: 1;
  height: 5px;
  -webkit-appearance: none;
  background: #ddd;
  border-radius: 5px;
  outline: none;
}

.rotation-slider input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 15px;
  height: 15px;
  background: #4caf50;
  border-radius: 50%;
  cursor: pointer;
}

.rotation-slider span {
  min-width: 60px;
  text-align: left;
  font-family: monospace;
}

.debug-text {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 10px;
  border-radius: 5px;
  margin: 10px 0;
  font-family: monospace;
  font-size: 14px;
}

.debug-text p {
  margin: 5px 0;
}
